/* Carousels */
.search__card {
  position: relative;
  background-image: url(../../../assets/img/background/my-card-bg.png);
  padding: 60px 0 150px;
  background-position-y: 45px;
  background-size: cover;
}
.search__card::before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  height: 47px;
  width: 100%;
  background: #fff;
}
